function dateEndShow(isComplete, date) {
    const d = new Date();
    let time = d.getTime();

    const remTime = date - time;
    const hour = Math.round((remTime % 86400000) / 3600000);
    const day = Math.floor(remTime / 86400000);

    if (isComplete) {
        return '  :)'
    } else if (date === '') {
        return ''
    } else if (remTime < 0) {
        return '  :('
    } else if (remTime <= 3600000) {
        return '   '
    } else if (remTime <= 86400000) {
        return `${hour} `
    } else {
        if (hour === 24) {
            return `${day + 1} `
        } else if (hour === 0) {
            return `${day} `
        } else {
            return `${day}   ${hour}  `
        }
    }

}

export { dateEndShow }